.Nav {
  display: flex;
  padding: 1rem 0;
  border-bottom: 1.5px solid #e0e0e0;
}

.category,
.active {
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
}

.active {
  padding: 0;
  color: #2c73e7;
}

.Nav :not(:first-child) {
  margin-left: 20px;
}

@media (max-width: 768px) {
  .Nav {
    overflow: auto;
    white-space: nowrap;
  }
  .category,
  .active {
    font-size: 0.9rem;
  }
}
