* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  word-break: break-all;
}

.App {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.date {
  border-bottom: 1.5px solid #e0e0e0;
}

.today {
  margin: 0;
  padding: 1rem 0;
  font-weight: 600;
}

@media (max-width: 768px) {
  .today {
    font-size: 0.9rem;
  }
}
