.Header {
  display: flex;
  width: 100%;
  height: 90px;
  align-items: center;
  border-bottom: 1.5px solid #e0e0e0;
}

.Header img {
  height: 60px;
  margin-right: 1rem;
}

.container {
  display: flex;
  width: 100%;
  height: 60px;
  align-items: flex-end;
  justify-content: space-between;
}

.form {
  position: relative;
  width: 180px;
  height: 30px;
}

.form input {
  width: 100%;
  height: 100%;
  padding: 5px 50px 5px 8px;
  border: 1.5px solid #b7b7b7;
  border-radius: 4px;
  outline: none;
  font-size: 0.9rem;
}

.buttons {
  display: flex;
  position: absolute;
  height: 100%;
  align-items: center;
  right: 0;
  top: 0;
}

.form button,
.form button[type="submit"] {
  padding: 1px 4px;
  border: none;
  cursor: pointer;
  background-color: inherit;
}

.form button {
  color: #b7b7b7;
}

.form button:disabled {
  /* color: #ffffff; */
}

.form button[type="submit"] img {
  height: inherit;
  margin: 0;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }
}
