.NewsList {
  list-style: none;
  padding: 0;
  margin: 0 auto;
}

.NewsItem {
  display: flex;
  align-items: center;
  padding: 1.5rem 0;
  border-bottom: 1px solid #e0e0e0;
}

.urlImg,
.urlImg img {
  height: 80px;
  width: 120px;
  object-fit: cover;
  margin-right: 1rem;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.content h4,
.content p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.content h4 {
  -webkit-line-clamp: 1;
  margin: 0;
}

.content p {
  -webkit-line-clamp: 2;
  margin: 0.5rem 0 0 0;
  line-height: 1.5;
  font-size: 0.9rem;
  color: #5f5f5f;
}

.Pagination {
  display: flex;
  justify-content: center;
  padding: 1rem 0 2rem;
}

@media (max-width: 768px) {
  .urlImg,
  .urlImg img {
    height: 70px;
    width: 110px;
  }
}
